#nav
{
    /* position: fixed; */
    display: flex;
    flex-direction: row;
    width: 375px;
    justify-content: space-between;
    align-items: center;
    padding: 14px 26px 12px 27px;
    gap: 205px;
    isolation: isolate;
    
    /* position: absolute; */
    height: 47px;
    left: 0%;
    right: 0%;
    
   
    /* border: solid; */
    top: calc(50% - 47px/2 - 382.5px);
    
    background: #FAF9F4;
}
.navRight{
    width: 78.26px;
height: 13px;




flex: none;
order: 1;
flex-grow: 0;
