.clock
{

    width: 54px;
    height: 22px;
    left: 0px;
    top: calc(50% - 22px/2 + 0.5px);
    
    /* Default/Body/Bold */
    
    font-family: 'SF Pro Text';
    font-style: normal;
    font-weight: 600;
    font-size: 17px;
    line-height: 22px;
    /* identical to box height, or 129% */
    
    text-align: center;
    letter-spacing: -0.408px;
    font-feature-settings: 'case' on;
    
    /* System Colours/Label Colour/Primary/Light */
    
    color: #000000;
}